<template>
    <div>  
        <div class="loginNav">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">{{title}}</p>
            <span class="f26 cfff pr-30" style="position:absolute;top:0;right:0;line-height:0.84rem;z-index:99;" @click="save">保存</span>
        </div>
        <div class="c333 f28" style="padding:0.84rem 0 1rem;">
            <div class="nameBox b_line">
                <van-uploader :after-read="onRead" accept="image/png, image/jpeg" style="width:100%;">
                    <img :src="$api.fileUrl + data.coverImage" alt="" v-if="data.coverImage" style="width:100%;height:100%;display:block;">
                    <div class="pt-90 pb-90 bs" v-else>
                        <img src="../../../assets/class/upLoadImg.png" alt="">
                        <p class="pt-60 cccc f32">上传封面</p>
                    </div>
                </van-uploader>
                <div class="nameInput">
                    <p>标题</p>
                    <input class="name c666" v-model="data.title" placeholder="请输入" type="text">
                </div>
            </div>
            <div class="content b_line">
                <p class="subtitle">班级简介</p>
                <textarea class="c666" v-if="$route.query.type == 1" v-model="data.honourDesc" placeholder="请输入简介内容..."></textarea>
                <textarea class="c666" v-else v-model="data.mienDesc" placeholder="请输入简介内容..."></textarea>
                
            </div>
            <div class="content">
                <p class="subtitle">图片</p>
                <div class="picture">
                    <img :src="$api.fileUrl + item" alt="" v-for="(item,index) in data.imageUrl" :key="index" accept="image/png, image/jpeg"  @click="show(item)">
                    <van-uploader :after-read="onReadImg">
                        <img src="../../../assets/my/upload.png" alt="" v-if="data.imageUrl.length < 10">
                    </van-uploader>
                </div>
                <p class="subtitle">视频</p>
                <div class="picture">
                    <video :src="$api.fileUrl + item" alt="" v-for="(item,index) in data.videoUrl" :key="index" @click="videoPlay(item)"></video>
                    <van-uploader :after-read="onReadVideo" accept="video/*">
                        <img src="../../../assets/my/upload.png" alt="" v-if="data.videoUrl.length < 1">
                    </van-uploader>
                </div>
            </div>  
        </div>
        <van-popup v-model="shows" @clickOverlay="clickOverlay">
            <video :src="videosrc" id="videos" controls style="width:100vw;">
            </video>
        </van-popup>
    </div>
</template>

<script>
import { ImagePreview } from 'vant';
export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            title:'',
            data:{
                imageUrl:[],
                videoUrl:[],
                coverImage:''
            },
            shows:false,
            videosrc:''
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        save(){
            if(this.$route.query.type == 1){
                var param = {
                    classesId : JSON.parse(localStorage.getItem('zhxy_user')).classesId,
                    coverImage : this.data.coverImage,
                    honourDesc : this.data.honourDesc,
                    imageUrls : this.data.imageUrl.toString(),
                    videoUrls : this.data.videoUrl.toString(),
                    teacherId : JSON.parse(localStorage.getItem('zhxy_user')).id,
                    title : this.data.title,
                }
                if(this.$route.query.id){
                    param.id = this.$route.query.id
                }
                this.$http_json(this.$api.class.insertOrUpdateInfoHonour,param).then(data => {
                    console.log(data);
                    if(data.code == 0){
                        this.$router.push({path:'/honorList',query:{type:1}})
                    }
                })
            }else{
                var param = {
                    classesId : JSON.parse(localStorage.getItem('zhxy_user')).classesId,
                    coverImage : this.data.coverImage,
                    mienDesc : this.data.mienDesc,
                    imageUrls : this.data.imageUrl.toString(),
                    videoUrls : this.data.videoUrl.toString(),
                    teacherId : JSON.parse(localStorage.getItem('zhxy_user')).id,
                    title : this.data.title,
                }
                if(this.$route.query.id){
                    param.id = this.$route.query.id
                }
                this.$http_json(this.$api.class.insertOrUpdateInfoMien,param).then(data => {
                    console.log(data);
                    if(data.code == 0){
                        this.$router.push({path:'/honorList',query:{type:2}})
                        // data.data.coverImage = this.$api.fileUrl + data.data.coverImage;
                        // this.data = data.data;
                    }
                })
            }
            
            // this.$router.push({path:"/classIntro"});
        },
        getHonorDetail(){//获取荣誉详情
            this.$http({url:this.$api.class.getHonorDetail},{id:this.$route.query.id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    // data.data.coverImage = this.$api.fileUrl + data.data.coverImage;
                    this.data = data.data;
                }
            })
        },
        getMienDetail(){//获取风采表情栏详情
            this.$http({url:this.$api.class.getMienDetail},{id:this.$route.query.id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    // data.data.coverImage = this.$api.fileUrl + data.data.coverImage;
                    this.data = data.data;
                }
            })
        },
        show(url){
            ImagePreview([this.$api.fileUrl + url]);
        },
        onRead(file){
            var form = new FormData();
            form.append('file',file.file);
            this.$fileHttp(this.$api.fileUpload,form).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.data.coverImage = data.data[0];
                }
            })
        },
        onReadImg(file){
            var form = new FormData();
            form.append('file',file.file);
            this.$fileHttp(this.$api.fileUpload,form).then(data => {
                console.log(data);
                if(data.code == 0){
                    console.log(this.data.imageUrl)
                    this.data.imageUrl.push(data.data[0]);
                }
            })
        },
        onReadVideo(file){
            var form = new FormData();
            form.append('file',file.file);
            this.$fileHttp(this.$api.fileUpload,form).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.data.videoUrl.push(data.data[0]);
                }
            })
        },
        videoPlay(url){
            this.shows = true;
            this.videosrc = this.$api.fileUrl + url;
            // this.$refs.v1.src=this.$api.fileUrl + url;
            // document.getElementById("videos").load();
        },
        clickOverlay(){
            document.getElementById("videos").pause()
        }
    },
    created() {
        var type=this.$route.query.type;
        var id=this.$route.query.id;
        if(type==1 && id){
            this.title = '班级荣誉编辑';
            this.getHonorDetail();
        }else if(type==2 && id){
            this.title = '表扬栏编辑';
            this.getMienDetail();
        }else{
            this.title = '发布文章';
        }
    }
}
</script>

<style scoped>
     /* 修改组件样式 */
    .loginNav{
        position: fixed;
        z-index: 999;
    }
    .van-tabbar-item--active {color: #8a9cff;}
    .nameBox{
        padding:.3rem .3rem 0;
    }
    .nameBox .bs{
        width:100%;
        height:3.88rem;
        box-shadow: 0 0 0.3rem #ccc;
        border-radius: 0.1rem;
        text-align: center;
    }
    .nameBox img{
        width:1rem;
    }
    .nameInput{
        padding:0 .3rem;
        line-height: 1.1rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .nameInput input{
        height:.6rem;
    }
    .name{
        border:0;
        text-align: right;
        width:60%;
    }
    .content{
        padding:0 .3rem .3rem;
    }
    .subtitle{
        line-height: .9rem;
    }
    .content textarea{
        width:100%;
        height:2.67rem;
        border:0;
    }
    .picture img{
        width:1.2rem;
        height:1.2rem;
        margin-bottom:.1rem;
        margin-left: 0.1rem;
    }
    .picture img:first-child{
        margin-left: 0;
    }
    .picture video{
        width:1.2rem;
        height:1.2rem;
        margin-bottom:.1rem;
        margin-left: 0.1rem;
        float: left;
    }
    .picture video:first-child{
        margin-left: 0;
    }
</style>




